.info-box {
    position: sticky;
    top: 0;
    background-color: #000;
}

.info-content h2 {
    color: #222;
    font-size: 7vh;
    padding: 30px 70px;
}

.info-slider-text {
    position: sticky;
    top: 0;
    background-color: #fff;
    overflow: hidden;
    border-top: 1px solid #222;
    border-bottom: 1px solid #222;
    transition: all .3s;
}

.info-slider-text h2 {
    font-size: 3em;
    font-weight: 700;
    font-family: "PretendardRegular";
    white-space: nowrap;
    color: #000;
    padding: 16px 0;
    transition: all .3s;
}

.info-slider-text:hover {
    background-color: #000;
}

.info-slider-text:hover h2 {
    color: rgb(174, 235, 96);
}

@media screen and (max-width: 973px) {
    .info-box {
        height: auto !important;
    }

    .info-content h2 {
        font-size: 30px;
        padding: 20px 0;
    }
}