.footer-box {
    background-color: #fff;
    position: sticky;
    top: 0;
}

.footer-img img {
    width: 300px;
}

.footer-link {
    width: 155px;
}

.footer-link span {
    font-size: 2vh;
    color: #000;
    font-weight: 700;
    padding-bottom: 16px;
    display: inline-block;
}

.footer-link ul li a {
    font-size: 18px;
    font-weight: 500;
    color: #000;
    transition: all .3s;
}

.footer-link ul {
    display: flex;
    flex-direction: column;
    gap: 6px
}

.footer-link ul li a svg {
    margin-left: 10px;
}

.footer-link ul li:hover a {
    color: #999;
    transform: translateX(4px);
}

@media screen and (max-width: 973px) {
    .footer-img img {
        width: 150px;
        padding-bottom: 20px;
    }

    .info-content h2 {
        font-size: 30px;
        padding: 20px 0;
    }
}