/* font family */
@font-face {
    font-family: "PretendardRegular";
    src: local("PretendardRegular"),
        url("./fonts/Pretendard-Regular.otf") format("truetype");
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h2,
h4,
h6,
p,
ul {
    margin-bottom: 0;
}

ul {
    list-style-type: none;
    padding-left: 0;
}

a {
    display: inline-block;
    text-decoration: none;
}


/* bootstrap css fixed */
.container {
    max-width: 2500px;
    padding: 3vh 5vw;
}

/* pages stylesheet */
.img-amin-box,
.roadmap-box {
    padding-top: 130px;
}

.readmap-content p {
    font-size: 1.6em;
    font-weight: 400;
    padding: 20px 0;
    color: #000;
}

.readmap-content h2 {
    font-size: 6.5em;
    color: #000;
    line-height: 1;
    font-weight: 600;
}

/* utility */
.utility-box {
    margin-top: 60px;
    height: 100vh;
    overflow: hidden;
}

.img-amin-box {
    position: relative;
}


.img-box-1 {
    width: 60vh;
    height: 60vh;
    object-fit: cover;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) rotate(-10deg);
}

.img-box-2 {
    width: 60vh;
    height: 60vh;
    object-fit: cover;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) rotate(10deg);
}

.img-box-3 {
    width: 60vh;
    height: 60vh;
    object-fit: cover;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) rotate(-20deg);
}

.img-box-4 {
    width: 60vh;
    height: 60vh;
    object-fit: cover;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) rotate(20deg);
}

.img-box-5 {
    width: 60vh;
    height: 60vh;
    object-fit: cover;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) rotate(-6deg);
}



.utility-info h2 {
    font-size: 20vh;
}

.utility-info h2:last-of-type {
    margin-left: 50%;
}

.ulitity-info-bottom h2 {
    font-size: 3.5em;
    color: #000;
}

.ulitity-info-bottom p {
    font-size: 1.6em;
    font-weight: 500;
    color: #000;
    padding: 20px 0;
}

.utility-info-content h2 {
    font-size: 4em;
    color: #000;
}

@media screen and (max-width: 973px) {
    .readmap-content h2 {
        font-size: 50px;
    }

    .img-amin-box img {
        max-height: 40vh;
        max-width: 40vh;
    }

    .utility-info h2:last-of-type {
        margin-left: 0;
    }

    .utility-info-content h2 {
        font-size: 3em;
    }

    .ulitity-info-bottom h2 {
        font-size: 6vh;
    }

    .utility-info h2 {
        font-size: 12vh;
    }
}