.about-box {
    background-color: #ff606d;
    position: sticky;
    top: 0;
}

.about-content h2 {
    color: #000;
    font-size: 8vh;
    line-height: 1.1;
    font-weight: 600;
    padding: 0 70px;
}

.about-content-2 h6 {
    color: #000;
    font-size: 1.6em;
    letter-spacing: -0.01em;
    color: #fff;
    margin-top: 5rem;
}

@media screen and (max-width: 973px) {

    .about-content-2 h6,
    .about-content h2 {
        font-size: 30px;
        padding: 0;
    }
}