.nav-box {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 99;
    padding: 10px 0;
}

.nav-box ul:first-of-type {
    gap: 30px;
}

.nav-box ul:first-of-type li a {
    font-size: 20px;
    font-weight: 500;
    color: #000;
    font-family: "PretendardRegular";
}

.nav-box ul:first-of-type li a.active {
    color: #ff05ffb9;
}

.nav-box img {
    max-height: 66px;
}

/* .nav-box ul:nth-of-type(2) li a {
    color: #000;
} */

.nav-last-link > ul li button {
    font-size: 13px  !important;
    height: 35px;
    line-height: 35px;
    width: 138px;
    background-color: #000;
    text-align: center;
    color: #fff;
    border-radius: 16px;
    color: #dcff36 !important;
    font-family: "PretendardRegular";
}

.social-link-box{
    padding-right: 8vh !important;
}

.nav-box-content{
    position: relative;
}

.big-logo {
    position: absolute;
    left: 55%;
    transform: translateX(-50%)
}

@media screen and (max-width: 973px) {

    .nav-box img {
        max-height: 44px;
    }

    .nav-box {
        background-color: #fff;
    }

    .big-logo {
        display: none;
    }

    .nav-list-mode {
        display: none;
    }

    .icon-bar {
        width: 20px;
        cursor: pointer;
        display: inline-block;
    }

    .icon-bar span {
        display: block;
        height: 2px;
        width: 100%;
        background-color: #000;
        transition: all .3s;
    }

    .icon-bar-one.active {
        transform: rotate(45deg) translate(4px, 4px);
    }

    .icon-bar-two.active {
        opacity: 0;
    }

    .icon-bar-three.active {
        transform: rotate(-45deg) translate(4px, -4px);
    }

}