.banner-box {
    height: 100vh;
    background-color: #cecece;
    overflow: hidden;
    position: sticky;
    top: 0;
}

.banner-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 999;
}

.banner-content h6 {
    font-size: 3vh;
    font-family: "PretendardRegular";
}

.banner-content h2 {
    font-size: 14vh;
    font-weight: 600;
    color: #000;
    font-family: "PretendardRegular";
}

.banner-content a {
    font-size: 16px;
    font-family: 400;
    height: 42px;
    line-height: 42px;
    text-align: center;
    width: 180px;
    background-color: #000;
    color: #dcff36;
    border-radius: 18px;
    transition: all .3s;
    font-family: "PretendardRegular";
}

.banner-content a:hover {
    background-color: #dcff36;
    color: #000;
}

.banner-slide-img-04,
.banner-slider-img-03,
.banner-slide-img-02,
.banner-slide-img-01 {
    width: 40vh;
    top: -100%;
    left: 18vh;
    display: flex;
    flex-direction: column;
    gap: 10rem;
}

.banner-slide-img-04 img,
.banner-slider-img-03 img,
.banner-slide-img-02 img,
.banner-slide-img-01 img {
    width: 100%;
}

.banner-slide-img-04,
.banner-slide-img-02 {
    width: 34vh;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    gap: 10rem;
}

.banner-slider-img-03 {
    left: unset;
    right: 0;
    /* top: 0; */
}

.banner-slide-img-04 {
    left: unset;
    right: 24vh;
    top: 0;
}

@media screen and (max-width: 973px) {

    .banner-slider-img-03,
    .banner-slide-img-01 {
        width: 30vh !important;
    }

    .banner-slide-img-04,
    .banner-slide-img-02 {
        width: 20vh !important;
    }

    .banner-content {
        width: 100%;
        padding: 0 2rem;
    }

    .banner-content h2 {
        font-size: 8vh;
        padding: 10px 0;
    }

    .banner-slide-img-01 {
        left: 0;
    }

    .banner-slide-img-04 {
        right: 0;
    }
}